import React, { Component, Suspense } from 'react';
import { componentWillAppendToBody } from "react-append-to-body";
import { connect } from 'react-redux';
import { getThemeDataWithWallID } from './actions/themeActions'
import { BrandingUTM } from './constants';
import { brandingUrl, startUpUrl } from './utils';
const App = React.lazy(() => import('./app'));
const ErrorPage = React.lazy(() => import('./app/Components/ErrorPages'));
const PostPopUp = React.lazy(() => import('./app/Components/PostPopUp'));
const ReportMedia = React.lazy(() => import('./app/Components/UserComponents/ReportMedia'));
const SharePost = React.lazy(() => import('./app/Components/UserComponents/SharePost'));
const AppHead = React.lazy(() => import('./app/Components/Head/Head'));
const UserComponents = React.lazy(() => import('./app/Components/UserComponents'));

export const ADD_SCRIPT = url => {
  const script = document.head.appendChild(document.createElement("script"))
  script.src = url
  script.async = true
  script.defer = true
  document.body.appendChild(script)
}

export const WINDOW_ON_LOAD = () => {
  if (!window.jQuery) ADD_SCRIPT("https://cloud.taggbox.com/widget/themes/js/jquery.min.js");
  ADD_SCRIPT("https://cloud.taggbox.com/widget/themes/js/slackdown.js");
  window.onload = () => {
    if (!window.jQuery) ADD_SCRIPT("https://cloud.taggbox.com/widget/themes/js/jquery.min.js");
    ADD_SCRIPT("https://cloud.taggbox.com/widget/themes/js/slackdown.js");
  }
}

const MyComponent = ({ children }) => {
  return children;
}
const TaggboxThemes = (props) => {
  if (props.loader && props.loader.loader != null && props.error.errorWithMessage == null && !props.error.planLimit && (props.error.themeError == null || (Object.keys(props.error.themeError).length > 0 && props.error.themeError.error_code == 0) || props.wall?.Personalization.demoWall === 1))
    return <Suspense fallback={<></>}><App {...props} /></Suspense>
  else if (props.error.errorWithMessage || props.error.planLimit || props.error.themeError)
    return <Suspense fallback={<></>}><ErrorPage {...props} /></Suspense>
  else
    return null
}
const AppendedMyComponent = componentWillAppendToBody(MyComponent);
class WidgetApp extends Component {
  state = {
    errorData: null
  }
  componentDidMount() {
    const { wallID } = this.props;
    this.props.getThemeDataWithWallID(wallID, null)
    WINDOW_ON_LOAD()
  }
  componentDidUpdate(prevProps, prevState) {
    let demoWall = prevProps.appData.wall?.Personalization?.demoWall
    if (prevProps.appData.error?.themeError !== prevState.errorData) {
      this.setState({ errorData: prevProps.appData.error?.themeError })
      if (typeof window.onSetErrorCode === 'function' && this.state.errorData !== null) {
        window.onSetErrorCode(this.state.errorData, demoWall)
      }
    }
  }

  render() {

    const { appData, modalPop } = this.props;

    let brandingNetworks = appData.webFilters != '' && appData.webFilters ? appData.webFilters.map(({ networkId }) => networkId) : null;
    const startUpDetails = startUpUrl(brandingNetworks, appData.wallID);
    return <>
      {appData && appData.wall && Object.keys(appData.wall).length > 0 ? <Suspense fallback={<></>}><AppHead wall={appData.wall} /></Suspense> : null}
      <AppendedMyComponent>  <Suspense fallback={<></>}><UserComponents /></Suspense></AppendedMyComponent>

      <TaggboxThemes {...appData} />
      {appData && appData.wall && Object.keys(appData.wall).length > 0 && appData.wall.UserDetail && Object.keys(appData.wall.UserDetail).length > 0 && appData.wall.UserDetail.planId.includes('53') ?
        <div
          ref={(el) => {
            if (el) {
              el.style.setProperty('align-items', 'center', 'important');
              el.style.setProperty('display', 'flex', 'important');
              el.style.setProperty('width', '100%', 'important');
              el.style.setProperty('text-decoration', 'none', 'important');
              el.style.setProperty('justify-content', 'center', 'important');
              el.style.setProperty('color', '#0b57d0', 'important');
            }
          }}
        >{
            startUpDetails[1].map(item => item.isAnchor ? <a href={startUpDetails[0] + BrandingUTM} target="_blank" aria-label={item.text}
              ref={(el) => {
                if (el) {
                  el.style.setProperty('align-items', 'center', 'important');
                  el.style.setProperty('display', 'flex', 'important');
                  el.style.setProperty('width', 'auto', 'important');
                  el.style.setProperty('text-decoration', 'none', 'important');
                  el.style.setProperty('padding', '8px', 'important');
                  el.style.setProperty('color', '#0b57d0', 'important');
                }
              }}>
              {item.text}
            </a> : item.text)
          }
        </div> : null}

      {modalPop.isShowPopUp ? <AppendedMyComponent> <Suspense fallback={<></>}><PostPopUp wall={appData.wall} data={modalPop.data} languageSetting={appData.languageSetting} networkData={appData.networkData} postData={appData.postData} brandingNetworks={brandingNetworks} /></Suspense> </AppendedMyComponent> : null}

      {modalPop.reportStatus ? <AppendedMyComponent> <Suspense fallback={<></>}><ReportMedia item={modalPop.reportData} wall={appData.wall} /></Suspense> </AppendedMyComponent> : null}

      {modalPop.shareStatus ? <AppendedMyComponent> <Suspense fallback={<></>}><SharePost postLink={modalPop.shareData.link} userName={modalPop.shareData.author.name} /></Suspense> </AppendedMyComponent> : null}

      {appData && appData.wall && Object.keys(appData.wall).length > 0 && appData.wall.UserDetail && Object.keys(appData.wall.UserDetail).length > 0 && !appData.wall.UserDetail.planId.includes('53') ? <AppendedMyComponent>
        <a href={brandingUrl(brandingNetworks, appData.wallID)[0] + BrandingUTM} target="_blank" style={{ display: 'none' }}
          aria-label={brandingUrl(brandingNetworks, appData.wallID)[1]}>{brandingUrl(brandingNetworks, appData.wallID)[1]}</a></AppendedMyComponent> : null}
    </>
  }
}
const mapStateToProps = state => {
  return {
    appData: state.appData,
    modalPop: state.modalPop
  }
}
const mapDispatchToProps = dispatch => {
  return {
    getThemeDataWithWallID: (Id, heightEvent) => dispatch(getThemeDataWithWallID(Id, heightEvent))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(WidgetApp);

